<template>
  <div>


    <section class="all-cars" v-if="!oneCar">
      <header-page/>

    <section class="border-text">
      <form action="">
        <label>Seřadit:</label>
        <select id="sorted" name="sorted">
          <option value="nejlevnejsi" @change="seradit('nejlevnejsi')">Nejlevnější</option>
          <option value="nejdrazsi" @change="seradit('nejdrazsi')">Nejdražší</option>
          <option value="nenovejsi" @change="seradit('nejnovejsi')">Nejnovější</option>
          <option value="tachometr" @change="seradit('kilometry')">Stav tachometru</option>
        </select>
        <input type="submit">
      </form>
    </section>

      <section class="border-text">
          <h2>Seřadit:</h2>
        <button class="sort-btn" @click="seradit('nejlevnejsi')" >Nejlevnější</button>
        <button class="sort-btn" @click="seradit('nejdrazsi')">Nejdražší</button>
        <button class="sort-btn"  @click="seradit('nejnovejsi')">Nejnovější</button>
        <button class="sort-btn"  @click="seradit('kilometry')">Stav tachometru</button>
        <button class="sort-btn"  @click="seradit('vykon')">Nejvýkonější</button>

      </section>




   <div class="main-content">


     <section class="column left">
        <button class="filter-button" @click="this.odkrytZnacky">Značky {{this.symbolZnacky}}</button>
       <br>
       <div v-if="this.ukazatZnacky">
       <div  class="filter-znacky" v-for="zn in znacky" :key="zn._id">
         <h3>{{ zn }}</h3>
         <input name="active" type="checkbox" :id="zn" @change="pridatZnacku(zn)">

       </div>
         <hr style="width:100%" size="3" color=black>
       </div>


       <button class="filter-button" @click="this.odkrytKaroserie">Karoserie {{this.symbolKaroserie}}</button>
       <br>
       <div v-if="this.ukazatKaroserie">
       <div  class="filter-znacky" v-for="karo in karoserie" :key="karo._id">
         <h3>{{ karo }}</h3>
         <input name="active" type="checkbox" :id="karo" @change="pridatKaroserii(karo)">

       </div>

         <hr style="width:100%" size="3" color=black>
       </div>

       <br>
       <p>Cena:</p>
       <div  class="filter-znacky">
         <input v-model="maxCena" @change="vypisMaxCenu(maxCena)" placeholder="Do:" />
       </div>

       <div  class="filter-znacky">
         <input v-model="minCena" @change="vypisMinCenu(minCena)" placeholder="Od:" />
       </div>



     </section>



    <section class="column right" v-if="this.omezeni()">
      <section class="border-text-choose-car" v-for="car of Cars"
           :key="car._id"
      >
        <div class="carDescription">
        <h1 class="title-car">{{car.znacka}} {{car.model}}</h1>
        <img src="https://s4.gifyu.com/images/testImg.md.jpg" class="imgCar" alt="testImg.jpg" border="0" />
        <h3 class="">{{car.karoserie}}</h3>
        <h3 class="">{{car.motor}} {{car.vykon}} kW</h3>
        <p>{{car.rok}}</p>
        <br>

          <button class="showCar" @click="toCarDetail(car)">Detail auta</button>

        <br>
        <h2 class ="price-car-browsing">{{car.cena}} Kč</h2>
        </div>

      </section>
    </section>

    <section class="column right" v-else>
      <section class="border-text-choose-car" v-for="car of vybranaAuta"
               :key="car._id"
      >
        <div class="carDescription">
          <h1 class="title-car">{{car.znacka}} {{car.model}}</h1>
          <img src="https://s4.gifyu.com/images/testImg.md.jpg" class="imgCar" alt="testImg.jpg" border="0" />
          <h3 class="">{{car.karoserie}}</h3>
          <h3 class="">{{car.motor}} {{car.vykon}} kW</h3>
          <p>{{car.rok}}</p>
          <br>

          <button class="showCar" @click="toCarDetail(car)">Detail auta</button>

          <br>
          <h2 class ="price-car-browsing">{{car.cena}} Kč</h2>
        </div>

      </section>
    </section>
   </div>


    <footer-page/>
    </section>

    <section class="one-car" v-else>
      <header-page/>

      <img class="carImage"/>
      <h1 class="subtitle-car-browsing">{{choosenCar.znacka}} {{choosenCar.model}}</h1>
      <img src="https://s4.gifyu.com/images/testImg.md.jpg" class="imgCar" alt="testImg.jpg" border="0" />
      <h3 class="subtitle-car-browsing">{{choosenCar.karoserie}}</h3>
      <h3 class="subtitle-car-browsing">{{choosenCar.motor}} {{choosenCar.vykon}} kW</h3>
      <p>{{choosenCar.rok}}</p>
      <br>
      <button class="showCar" @click="oneCar=false">Zpet</button>

      <br>
      <h2 class ="price-car-browsing">{{choosenCar.cena}} Kč</h2>
      <footer-page />

    </section>

  </div>

</template>

<script>


import axios from "axios";


export default {
  name: 'carSelection',
  /*
  props:{
    brand:{
      type: String
    }
   }
   */

  data(){
    return {
      oneCar:false,
      choosenCar:{},

      Cars: [],
      vybranaAuta: [],
      zvoleneAuto:{},
      minCena: null,
      maxCena: null,

      znacky: [],
      vybraneZnacky:[],
      vsechnyZnacky: true,
      symbolZnacky:"▼",
      ukazatZnacky:false,

      karoserie:[],
      vybraneKaroserie:[],
      vsechnyKaroserie: true,
      symbolKaroserie:"▼",
      ukazatKaroserie:false,

    }
  },

  mounted() {
    axios.get('http://localhost:5000/api/posts/')
        .then((response) =>{
          //console.log(response.data);
          this.Cars = response.data
          this.getZnacky();
          this.getCaroseries();
          console.log(this.vsechnyZnacky, this.maxCena);

        })
        .catch((error) => {
          console.log(error);
        });
  },

  methods: {

    odkrytZnacky(){
      this.ukazatZnacky = !this.ukazatZnacky;
      if(this.ukazatZnacky){
        this.symbolZnacky = "▲";
      }else{
        this.symbolZnacky = "▼";
      }
    },
    odkrytKaroserie(){
      this.ukazatKaroserie= !this.ukazatKaroserie;
      if(this.ukazatKaroserie){
        this.symbolKaroserie = "▲";
      }else{
        this.symbolKaroserie = "▼";
      }
    },

    omezeni(){
      if(this.minCena === null && this.maxCena === null && this.vsechnyZnacky && this.vsechnyKaroserie){
        return true;
      }else{
        return false;
      }

    },

    //is selected car allready in array of vybranaAuta
    isInArray(id){
      let bool = false;
      for(let i=0; i< this.vybranaAuta.length; i++){
        if(this.vybranaAuta[i]._id === id){
          bool = true;
        }
      }
      return bool;
    },

    seradit(parametr){


      if(this.vybranaAuta.length ===0) {


        switch (parametr) {
          default:

              this.Cars.sort(function (a, b){
                return a.cena - b.cena;
              })
            break;

          case 'nejdrazsi':

            this.Cars.sort(function (a, b){
              return b.cena - a.cena;
            })
            break;

          case 'nejnovejsi':

            this.Cars.sort(function (a, b){
              return a.rok - b.rok;
            })
            break;

          case 'kilometry':

            this.Cars.sort(function (a, b){
              return a.najeto - b.najeto;
            })
            break;

          case 'vykon':

            this.Cars.sort(function (a, b){
              return b.vykon - a.vykon;
            })
            break;
        }

        }else{

        switch (parametr) {
          default:

            this.vybranaAuta.sort(function (a, b){
              return a.cena - b.cena;
            })
            break;

          case 'nejdrazsi':

            this.vybranaAuta.sort(function (a, b){
              return b.cena - a.cena;
            })
            break;

          case 'nejnovejsi':

            this.vybranaAuta.sort(function (a, b){
              return a.year - b.year;
            })
            break;

          case 'kilometry':

            this.vybranaAuta.sort(function (a, b){
              return a.najeto - b.najeto;
            })
            break;

          case 'vykon':

            this.vybranaAuta.sort(function (a, b){
              return b.vykon - a.vykon;
            })
            break;
        }


      }



    },

    vypisMinCenu(cena){
      this.minCena =cena;
      console.log('Min cena je ',this.minCena);
      if(this.minCena !== null){
          if(this.vybranaAuta.length === 0){
            this.vybranaAuta = this.Cars.filter(function (data) {
              return data.cena >= cena;
            });
          }else{
            this.filtruj();
          }
      }


    },
    vypisMaxCenu(cena){
      this.minCena =cena;
      console.log('Max cena je ',this.maxCena);

      if(this.maxCena !== null){
        if(this.vybranaAuta.length === 0){
          this.vybranaAuta = this.Cars.filter(function (data) {
            return data.cena <= cena;
          });
        }else{
          this.filtruj();
        }
      }
    },

      getZnacky(){
        this.vybraneZnacky =[];
        for(let i =0; i<this.Cars.length; i++){
          if( !(this.znacky.includes(this.Cars[i].znacka)))
              this.znacky.push(this.Cars[i].znacka);
        }
      },
    getCaroseries(){
      this.vybraneKaroserie =[];
      for(let i =0; i<this.Cars.length; i++){
        if( !(this.karoserie.includes(this.Cars[i].karoserie)))
          this.karoserie.push(this.Cars[i].karoserie);
      }
    },

    pridatZnacku(zn){

      if( (!this.vybraneZnacky.includes(zn)) || this.vybraneZnacky.length === 0) {
        this.vybraneZnacky.push(zn);
        this.vsechnyZnacky = false;

      }else{
        //remove from vybranaKategorie
        this.vybraneZnacky.splice(this.vybraneZnacky.indexOf(zn), 1);
        if(this.vybraneZnacky.length ===0){
          this.vsechnyZnacky = true;
        }
      }



      if(this.vybranaAuta.length === 0){
        let znackyFiltru = this.vybraneZnacky;
        this.vybranaAuta = this.Cars.filter(function (data){
          let bool = false;
          for(let i=0; i<znackyFiltru.length; i++){
            if (data.znacka === znackyFiltru[i]){
              bool = true;
            }
          }
          return bool;
        });
      }else {
        for(let i=0; i<this.Cars.length; i++){
          if( (this.vybraneZnacky.includes(this.Cars[i].znacka)) &&  !this.isInArray(this.Cars[i]._id)){
            this.vybranaAuta.push(this.Cars[i]);
          }
        }

      }
      this.filtruj();
      console.log('vybrane znacky',this.vybraneZnacky);

    },

    pridatKaroserii(karo){

      if( (!this.vybraneKaroserie.includes(karo)) || this.vybraneKaroserie.length === 0) {
        this.vybraneKaroserie.push(karo);
        this.vsechnyKaroserie = false;

      }else{
        //remove from vybranaKategorie
        this.vybraneKaroserie.splice(this.vybraneKaroserie.indexOf(karo), 1);
        if(this.vybraneKaroserie.length === 0){
          this.vsechnyKaroserie = true;
        }
      }


      if(this.vybranaAuta.length === 0){
        let karoserieFiltru = this.vybraneKaroserie;
        this.vybranaAuta = this.Cars.filter(function (data){
          let bool = false;
          for(let i=0; i<karoserieFiltru.length; i++){
            if (data.karoserie === karoserieFiltru[i]){
              bool = true;
            }
          }
          return bool;
        });
      }else {
        for(let i=0; i<this.Cars.length; i++){
          if( (this.vybraneKaroserie.includes(this.Cars[i].karoserie)) &&  !this.isInArray(this.Cars[i]._id)){
            this.vybranaAuta.push(this.Cars[i]);
          }
        }
        //this.filtruj();
      }
      this.filtruj();
      console.log('vybrane karoserie',this.vybraneKaroserie);


    },

    filtruj(){
      //this.vybranaAuta = this.Cars.filter(data => data.znacka === zn);
      let znackyF = this.vybraneZnacky;
      let karoserieF = this.vybraneKaroserie;

      if(this.vybraneZnacky.length !== 0) {
        this.vybranaAuta = this.Cars.filter(function (data) {
          let bool = false;
          for (let i = 0; i < znackyF.length; i++) {
            if (data.znacka === znackyF[i]) {
              bool = true;
            }
          }
          return bool;
        });

      }

      //2.filtr
      if(this.vybraneKaroserie.length !==0) {
        this.vybranaAuta = this.vybranaAuta.filter(function (data) {
          let bool = false;
          for (let i = 0; i < karoserieF.length; i++) {
            if (data.karoserie === karoserieF[i]) {
              bool = true;
            }
          }
          return bool;
        });

      }
      //3.filtr
      if(this.minCena !== null) {
        let cena = this.minCena;
        this.vybranaAuta = this.Cars.filter(function (data) {
          return data.cena >= cena;
        });
      }

      //4.filtr
      if(this.maxCena !== null) {
        let cena = this.maxCena;
        this.vybranaAuta = this.Cars.filter(function (data) {
          return data.cena <= cena;
        });
      }

    },



    toCarDetail(car){
      this.choosenCar = car;
      this.oneCar = true;


    },



  }
}
</script>

<style>
/*
.optionalCar{
  float: right;
  width: 75%;
  text-align: center;

}
.main-content:after {
  content: "";
  display: table;
  clear: both;
}
.main-content{
  display: flex;
}

 */

.column {
  float: left;


}
.left{
  width: 20%;
  border-style: solid;
  padding-left: 1em;
  padding-right: -1em;

  background: #ffee80;
  font-size: 20px;
  border-radius: 1em;


  margin-bottom: 5rem;
}

.filter-button{
  font-size: 18px;

  background:#ffee80;
}

.filter-znacky{
  font-size: 15px;
}

.right{
  width: 80%;
}

/* Clear floats after the columns */
.main-content:after {
  content: "";
  display: table;
  clear: both;
}
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}

.showCar{
  background-color: #008CBA;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */



.border-text-choose-car{
  text-align: center;


  background: #ffee80;
  font-size: 20px;
  border-radius: 1em;


  margin-bottom: 5rem;
}

.border-text
{
  width:100%;
  text-align: center;
}
.sort-btn
{
  background-color: #e7e7e7;
  color: black;
  border: none;
  /* color: white; */

  padding: 15px 32px;
  display: inline-block;
  font-size: 16px;
  border-style: solid;


}



</style>
